<script>
  import { onMount } from 'svelte';
  import ui from 'stores/UI';
  import draft from 'stores/Draft';
  import Subcomponents from './Subcomponents';

  export let toolbarHeight;
  let dialogElement;
  $: selectedMenu = $ui.selectedMenu;
  onMount(() => {
    document.body.addEventListener('pointerdown', onClick);
  });

  function onClick(event) {
    console.log('element', event.target);
    if (
      selectedMenu !== -1 &&
      //Disable click dismissal for select/repeat
      selectedMenu !== 4 &&
      !dialogElement.contains(event.target) &&
      !event.target.classList.contains('color-input-canvas')
    ) {
      console.log('close event preventDefault');
      event.preventDefault();
      ui.update((temp) => {
        temp.selectedMenu = -1;
      });
      document.body.removeEventListener('pointerdown', onClick);
    }
  }
</script>

<div class="dialog" style={`top: ${toolbarHeight}px`} bind:this={dialogElement}>
  <svelte:component this={Subcomponents[selectedMenu].component} />
</div>

<style>
  @media (min-width: 900px) {
    .dialog {
      width: 30vw;
      min-width: 30em;
    }
  }

  @media (max-width: 900px) {
    .dialog {
      top: 0 !important;
    }
  }

  .dialog {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    overflow: auto;

    z-index: 10;
    background-color: var(--color-5);
    padding: 20px;
    border: 1px solid var(--color-4);
  }
</style>
